/**
* Slider using FLICKITY
*/

const flickity = () => {
  const slider = document.querySelector('.slider-js-badges');
  const carouselIndex = document.querySelector('.top__badges-slider-index');
  const carouselArray = document.querySelector('.top__badges-slider-array');

  //@ts-ignore
  const flkty = new Flickity( slider, {
    cellAlign: 'left',
    contain: true,
    pageDots: false,
    draggable: false,
  });

  //for index and arrray
  const updateStatus = () => {
    const slideNumber = flkty.selectedIndex + 1;
    //@ts-ignore
    carouselIndex.textContent = slideNumber + '/' + flkty.slides.length;
  }
  updateStatus();
  
  const previousButton = document.querySelector('.carousel--prev');
  previousButton?.addEventListener('click', function () {
    flkty.previous();
  });
  
  const nextButton = document.querySelector('.carousel--next');
  nextButton?.addEventListener('click', function () {
    flkty.next();
  });
  
  flkty.on( 'select', updateStatus );
}

export default flickity