const faq = () => {
  const accrdn = document.querySelectorAll('.js-faq-item');
  const qstn = document.querySelectorAll('.js-faq-item-question')

  for(let x = 0; x < accrdn.length; x++) {
    qstn[x].addEventListener('click', () => {
      accrdn[x].classList.toggle('wills-trusts__faq-item--animate');
    })
  }
}

export default faq;