const nav = () => {
  let menubtn = document.querySelector('.js-header-menu__btn');
  let menudwn = document.querySelector('.js-header-menu');
  const header = document.querySelector('.header')

  menubtn?.addEventListener("click", () => {
    menudwn?.classList.toggle("header-menu__animate");
    menubtn?.classList.toggle("header-menu__btn-animate");
  })

  if(header) {
    window.addEventListener('scroll', () => {
      if(window.pageYOffset >= 100) {
        header?.classList.add('header--fixed');
      } else {
        header?.classList.remove('header--fixed');
      }
    })
  }
}

export default nav