const dropdown = () => {
  const sub = document.querySelectorAll('.js-sub-item');
  const link = document.querySelectorAll('.js-link-item')

  for(let x = 0; x < sub.length; x++) {
    link[x].addEventListener('click', () => {
      sub[x].classList.toggle('header-menu__dropdown-item--animate');
    })
  }
}

export default dropdown;